import PortfolioList from "../portfolioList/PortfolioList"
import { useEffect, useState } from "react"
import "./portfolio.scss"
import {featuredPortfolio,webPortfolio,mobilePortfolio,softwarePortfolio,gamePortfolio} from "../../data.js"


export default function Portfolio() {

    const [selected,setSelected] = useState("featured");
    const [data,setData] = useState([]);

    const list =[
        {
            id:"featured",
            title:"Featured"
        },
        {
            id:"web",
            title: "Web App"
        },
        {
            id:"mobile",
            title: "Mobile App"
        },
        {
            id:"software",
            title: "Software"
        },
        {
            id:"game",
            title:"Game"
        }
    ];

    useEffect(() =>{

        switch(selected){
            case "featured":
                setData(featuredPortfolio);
                break;
            case "web":
                setData(webPortfolio);
                break;
            case "mobile":
                setData(mobilePortfolio);
                break;
            case "software":
                setData(softwarePortfolio);
                break;
            case "game":
                setData(gamePortfolio);
                break;
            default:
                setData(featuredPortfolio);
        }

    },[selected])

    return (
        <div className="portfolio" id="portfolio">
            <h1>Portfolio</h1>
            <ul>
                {list.map((item) => (
                <PortfolioList 
                    title={item.title} 
                    active={selected === item.id}
                    setSelected={setSelected}
                    id = {item.id}
                />
                ))}
            </ul>
            <div className="container">
                {data.map((d) => ( 
                <div className="item">
                    <img src={d.img} alt=""/>
                    <h3>{d.title}</h3>
                </div>
                ))}
            </div>
        </div>
    )
}
